import { WidgetBase } from '@dojo/widget-core/WidgetBase';
import { v, w } from '@dojo/widget-core/d';
import { theme, ThemeableMixin } from '@dojo/widget-core/mixins/Themeable';
import { Link } from '@dojo/routing/Link';

import * as css from './styles/article.m.css';

export interface ArticleItem {
	id: number | null;
	title: string;
	user: string | null;
	url: string;
	points: string | null;
	time_ago: string;
	comments_count: number;
	loading: boolean;
}

interface ArticleProperties {
	item: ArticleItem;
	index: number;
	pageNumber: number;
	loading: boolean;
}

const articleShell = [
	v('h2', [
		v('a', { classes: { a: false } }, [ 'Loading...' ])]),
		v('p', { classes: { a: false } }, [ v('a'), v('a') ])
	];

@theme(css)
export class Article extends ThemeableMixin(WidgetBase)<ArticleProperties> {

	private _renderArticle(article: ArticleItem) {
		const { url, title, points, user, id, comments_count, time_ago } = article;
		const commentText = comments_count === 0 ? 'discuss' : `${comments_count} comments`;

		return [
			v('h2', [
				v('a', { href: url, target: 'none', classes: this.classes(css.title) }, [ title ])
			]),
			v('p', { classes: this.classes(css.details) }, [
				`${points || 0} points ${user ? 'by ' : ''}`,
				user ? w(Link, {
					key: 'user',
					to: 'user',
					params: { user },
					classes: this.classes(css.link)
				}, [
					user
				]) : null,
				` ${time_ago} `,
				w(Link, {
					key: 'comments',
					to: 'comments',
					params: { id: `${id}` },
					classes: this.classes(css.link)
				}, [ commentText ])
			])
		];

	}

	protected render() {
		const { item, index, pageNumber, loading } = this.properties;
		const articleNumber = `${(pageNumber - 1) * 30 + index + 1}`;

		return v('article', { classes: this.classes(css.root) }, [
			v('span', { classes: this.classes(css.pageNumber) }, [ articleNumber ]),
			v('div', {
				key: index,
				classes: this.classes(css.post)
			}, loading ? articleShell : this._renderArticle(item))
		]);
	}

}



